<template>
<div>
  <!---->
  <el-form :model="queryParams" ref="queryForm" :inline="true" label-width="68px">
    <el-form-item label="" prop="ipaddr">
      <el-input
          v-model="queryParams.ipaddr"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
      />
    </el-form-item>
    <el-form-item label="" prop="userName">
      <el-input
          v-model="queryParams.userName"
          placeholder=""
          clearable
          size="small"
          @keyup.enter.native="handleQuery"
      />
    </el-form-item>
    <el-form-item>
      <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
      <el-button type="primary" plain icon="el-icon-refresh" size="mini" @click="resetQuery"></el-button>
    </el-form-item>
  </el-form>

<!---->
  <el-table
      :data="tableData"
      stripe
      style="width: 100%">

    <el-table-column
        prop="token"
        label=""
        align="center"
        width="220">
    </el-table-column>

    <el-table-column
        prop="ryxm"
        label=""
        align="center"
        width="160">
    </el-table-column>

    <el-table-column
        prop="rybm"
        label=""
        align="center"
        width="160">
    </el-table-column>

    <el-table-column
        prop="logintime"
        label=""
        align="center"
        width="220">
    </el-table-column>

    <el-table-column
        prop="ip"
        label="ip"
        align="center"
        width="220">
    </el-table-column>

    <el-table-column
        prop="browserName"
        label=""
        align="center"
        width="180">
    </el-table-column>

    <el-table-column
        prop="osName"
        label=""
        align="center"
        width="180">
    </el-table-column>

    <el-table-column
        prop="lsdate"
        label=""
        align="center">
<!--      <template slot-scope="scope">
        <div>{{this.lssession}}</div>
      </template>-->
    </el-table-column>

    <el-table-column
        prop=""
        label=""
        align="center"
        width="120">
      <template slot-scope="scope">
        <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleForceLogout(scope.row.logintoken)"
        ></el-button>
      </template>
    </el-table-column>

  </el-table>
</div>
</template>

<script>
export default {
name: "zxyh",
  data(){
  return{
    queryParams: {
      ipaddr: undefined,
      userName: undefined
    },

    /**/
    queryInfo: {
      query: '', //
      pagenum: 1,  //
      pagesize: 10 //
    },

    tableData:[],
  }
  },
  created() {
    this.getList();
  },
  methods:{
 /***/
    getList:function (){
    this.$http.get("/xtjk/zxyh").then(res=>{
      this.tableData=res.data.data

      //
      for(var i=0;i<this.tableData.length;i++){
        this.$set(this.tableData[i],"lsdate",this.tableData[i].logintoken==window.sessionStorage.getItem('token')?'':'')
      }
      console.log(this.tableData)
    })
    },

    /**  */
    handleQuery() {
      this.pageNum = 1;
      this.getList();
    },

    /**  */
    resetQuery() {
/*      this.resetForm("queryForm");
      this.handleQuery();*/
      this.getList()
    },

    /**/
    handleForceLogout:function (token){
      this.$http.get("/xtjk/handleForceLogout?token="+token).then(res=>{
        if(res.data.code==200){
          this.$message.success("")
          this.getList()
        }else this.$message.error("")
      })
    }
  }
}
</script>

<style scoped>

</style>
