<template>
  <div>
    <!---->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input placeholder="" v-model="queryInfo.query" clearable
                    @clear="getUserList"> <!--clearable@clear-->
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addialogVisiable=true"></el-button>
          <el-button type="primary" @click="testfr"></el-button>
        </el-col>
      </el-row>
    </el-card>

    <!---->
    <el-table :data="userList" border stripe style="width: 100%">
      <el-table-column type="index" label="" width="60"></el-table-column>
      <el-table-column prop="rybm" label="" width="100"></el-table-column>
      <el-table-column prop="ryxm" label="" width="100"></el-table-column>
      <el-table-column prop="xb" label="" width="80"></el-table-column>
      <el-table-column prop="sjhm" label="" width="120"></el-table-column>
      <el-table-column prop="sfzhm" label="" width="180"></el-table-column>
      <el-table-column label=""></el-table-column>
      <el-table-column prop="qyzt" label="" width="80">
        <template slot-scope="scope">
          <el-switch
              v-model="scope.row.qyzt"
              active-value="1"
              inactive-value="0"
              @change="userstatechange(scope.row)"
              active-color="#13ce66"
              inactive-color="#ff4949">
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column label="" width='120'>
        <template slot-scope="scope">

          <el-tooltip class="item" effect="dark" content="" placement="top"> <!---->
            <!---->
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.rybm)"></el-button>
          </el-tooltip>

          <el-tooltip class="item" effect="dark" content="" placement="top"> <!---->
            <!---->
            <el-button type="primary" icon="el-icon-delete" size="mini" @click="DeleteUser(scope.row.rybm)"></el-button>
          </el-tooltip>

        </template>
      </el-table-column>

    </el-table>


    <!---->
    <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
    </el-pagination>


    <!---->
    <el-dialog
        title=""
        :visible.sync="addialogVisiable" @close="addDialogClose"
        width="50%">
      <!---->

      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef">

        <el-row :gutter="20">
          <el-col :span="6"><div class="grid-content bg-purple">
            <!---->
            <el-form-item label="" prop="rybm">
              <el-input v-model="addForm.rybm"></el-input>
            </el-form-item>
          </div>
          </el-col>
          <el-col :span="6"><div class="grid-content bg-purple">
            <!---->
            <el-form-item label="" prop="ryxm">
              <el-input v-model="addForm.ryxm"></el-input>
            </el-form-item>
          </div>
          </el-col>
          <el-col :span="6"><div class="grid-content bg-purple">
            <!---->
            <el-form-item label="" prop="password">
              <el-input v-model="addForm.password"></el-input>
            </el-form-item>
          </div></el-col>

          <el-col :span="6"><div class="grid-content bg-purple">
            <!---->
            <el-form-item label="" prop="xb">
              <el-select v-model="addForm.xb" placeholder="">
                <el-option
                    v-for="item in sexOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item></div></el-col>
        </el-row>


        <el-row :gutter="20">
          <el-col :span="6"><div class="grid-content bg-purple">
            <!---->
            <el-form-item label="" prop="sjhm">
              <el-input v-model="addForm.sjhm"></el-input>
            </el-form-item>
          </div></el-col>
          <el-col :span="6"><div class="grid-content bg-purple">
            <!--email-->
            <el-form-item label="" prop="email">
              <el-input v-model="addForm.email"></el-input>
            </el-form-item>
          </div></el-col>
          <el-col :span="12"><div class="grid-content bg-purple">

            <!---->
            <el-form-item label="" prop="sfzhm">
              <el-input v-model="addForm.sfzhm"></el-input>
            </el-form-item>
          </div></el-col>
        </el-row>

      </el-form>
      <!---->
      <span slot="footer" class="dialog-footer">
    <el-button @click="addialogVisiable = false"> </el-button>
    <el-button type="primary" @click="addusersubmit"> </el-button>
  </span>
    </el-dialog>

    <el-dialog
        title=""
        :visible.sync="EditdialogVisible"
        width="50%">
      <!---->
      <el-form>

        <el-row :gutter="20">
          <el-col :span="6"><div class="grid-content bg-purple">
            <!---->
            <el-form-item label="" prop="rybm">
              <el-input v-model="EditForm.rybm" disabled></el-input>
            </el-form-item>
          </div>
          </el-col>
          <el-col :span="6"><div class="grid-content bg-purple">
            <!---->
            <el-form-item label="" prop="ryxm">
              <el-input v-model="EditForm.ryxm"></el-input>
            </el-form-item>
          </div>
          </el-col>
          <el-col :span="6"><div class="grid-content bg-purple">
            <!---->
            <el-form-item label="" prop="password">
              <el-input v-model="EditForm.password"></el-input>
            </el-form-item>
          </div></el-col>

          <el-col :span="6"><div class="grid-content bg-purple">
            <!---->
            <el-form-item label="" prop="xb">
              <el-select v-model="EditForm.xb" placeholder="">
                <el-option
                    v-for="item in sexOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                ></el-option>
              </el-select>
            </el-form-item></div></el-col>
        </el-row>


        <el-row :gutter="20">
          <el-col :span="6"><div class="grid-content bg-purple">
            <!---->
            <el-form-item label="" prop="sjhm">
              <el-input v-model="EditForm.sjhm"></el-input>
            </el-form-item>
          </div></el-col>
          <el-col :span="6"><div class="grid-content bg-purple">
            <!--email-->
            <el-form-item label="" prop="email">
              <el-input v-model="EditForm.email"></el-input>
            </el-form-item>
          </div></el-col>
          <el-col :span="12"><div class="grid-content bg-purple">

            <!---->
            <el-form-item label="" prop="sfzhm">
              <el-input v-model="EditForm.sfzhm"></el-input>
            </el-form-item>
          </div></el-col>
        </el-row>

      </el-form>

      <span slot="footer" class="dialog-footer">
    <el-button @click="EditdialogVisible = false"> </el-button>
    <el-button type="primary" @click="submitEditdialogVisible"> </el-button>
  </span>
    </el-dialog>


  </div>
</template>

<script>
export default {
  name: "Users",
  data() {
    return {
      //
      addFormRules: {
        rybm: [
          {required: true, message: '', trigger: 'change'},],
        ryxm: [
          {required: true, message: '', trigger: 'change'},
          /*  {min: 5, max: 5, message: '5', trigger: 'change'}*/],
        password: [
          {required: true, message: '', trigger: 'change'},
          /*{min: 5, max: 5, message: '5', trigger: 'change'}*/],
        sjhm: [
          {required: true, message: '', trigger: 'change'}],
        email: [
          {required: true, message: '', trigger: 'change'}],
/*        sfzhm: [
          {required: true, message: '', trigger: 'change'},]*/
      },

      //
      addForm: {
        rybm:'',
        ryxm: '',
        password: '',
         xb:'',//select
        sjhm: '',
        email: '',
        sfzhm:''
      },
      EditForm:{},//
      queryInfo: {
        query: '', //
        pagenum: 1,  //
        pagesize: 10 //
      },
      userList: [],
      total: 10,

      addialogVisiable: false, //
      EditdialogVisible:false,//

    }
  },
  created() {
    this.getUserList()
  },
  methods: {

    //
    getUserList: function () {
      this.$http.post("/user/queryrybm", JSON.stringify(this.queryInfo)).then(res => {
        this.userList = res.data.data
      })
    },

    /*pagesize*/
    handleSizeChange: function (newsize) {
      this.queryInfo.pagesize = newsize
      this.getUserList()
    },

    /**/
    handleCurrentChange: function (newpage) {
      //console.log(newpage)
      this.queryInfo.pagenum = newpage
    },

    //switch
    userstatechange(data) {
      this.$http.get("/user/updateRybm?rybm="+data.rybm+'&qyzt='+data.qyzt).then(res => {
        if (res.data.code == '200') {
          this.$message.success("")
          this.getUserList() //
        } else {
          this.$message.error("" + res.data.data)
        }
      })
    },

    //()
    addDialogClose: function () {
      this.$refs.addFormRef.resetFields()
    },

    //
    addusersubmit:function (){
      this.$refs.addFormRef.validate((valid) => {
        if(valid){
          console.log(JSON.stringify(this.addForm))
          this.loading = true;
          this.$http.post('/user/insertrybm',JSON.stringify(this.addForm)).then(res=>{
            if(res.data.code=='200'){
                this.$message.success("")
                this.addialogVisiable=false
                this.getUserList() //
            }else this.$message.error("")
          })
        }
      })
    },
    //
    DeleteUser:function (rybm){
      this.$http.get('/user/DeleteUser?rybm='+rybm).then(res=>{
        if(res.data.code=='200'){
          this.$message.success("")
          this.getUserList() //
        } else this.$message.error("")
      })
    },

    //
    showEditDialog:function (rybm){
      this.queryInfo.query=rybm
      this.$http.post("/user/queryrybm", JSON.stringify(this.queryInfo)).then(res => {
        this.queryInfo.query=''
        console.log(res.data.data[0])
        this.EditForm=res.data.data[0]
      })
      this.EditdialogVisible=true
    },



    //
    submitEditdialogVisible:function (){
      this.$http.post('',JSON.stringify(this.EditForm).then(res=>{
        if(res.data.code=='200'){
          this.$message.success("")
          this.getUserList() //
        }else this.$message.error("")
      }))
    },
    //
    testfr:function (){
       var reportlets ="[{reportlet: 'fpdy%2Fykgl%2Fykgl_ckd.cpt',yljgbm:'000001',kfbm:'01',ckdh:'CK01201904000053'}]";
       this.FrPrint(reportlets)
    }

  }
}
</script>

<style scoped>
.el-input{
  margin: 0;
}
</style>
