<template>

 <!--<el-button type="info" @click="logout"></el-button>-->
  <el-container class="home-container">
    <!--//-->
    <el-header>
      <div>
        <img src="../assets/img/login.png" alt="" style="">
        <span>XX</span>
      </div>

      <el-dropdown>
        <el-button type="primary" size="small" style="background-color:green">
          <i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
<!--        <div class="avatar-wrapper">
          <img src="../assets/img/narbar.jpg" class="user-avatar">
          <i class="el-icon-caret-bottom" />
        </div>-->
        <el-dropdown-menu slot="dropdown">
          <!---->
          <router-link to="/message" style="text-decoration: none">
          <el-dropdown-item></el-dropdown-item>
          </router-link>

          <el-dropdown-item @click.native="logout"></el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <!--<el-button type="info" @click="logout"></el-button>-->

    </el-header>
   <!-- -->

    <el-container>
      <!---->
      <el-aside :width="iscollapse?'64px':'200px'">
        <div class="toggele-button" @click="togglecollapse">|||</div>
     <!--   -->
        <el-menu
            :default-active="this.activePth"
            class="el-menu-vertical-demo" :unique-opened="true" :collapse="iscollapse"
            :collapse-transition="false" :router="true"
            background-color="#545c64"
            text-color="#fff"
            active-text-color="#409BFF">

          <!---->
          <el-submenu :index="item.mkbm1+''" v-for="item in menList" :key="item.mkbm1">
            <!---->
            <template slot="title">
              <!---->
              <i :class="item.icon"></i>
              <!---->
              <span>{{item.mkmc1}}</span>
            </template>

           <!-- -->
            <el-menu-item :index="'/'+subitem.path" v-for="subitem in item.children"
                          :key="subitem.mkbm2" @click="saveNavState('/'+subitem.path)">
              <template slot="title">
                <!---->
                <i class="el-icon-menu"></i>
                <!---->
                <span>{{subitem.mkmc2}}</span>
              </template>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <!---->
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>

</template>

<script>
export default {
name: "home",
  data(){
  return{
    //
    menList:[],

    iscollapse:false,//false

    //
    activePth:''
  }
  },
  created() {
  this.getMenuList()
/*    this.menList=[
      {authName:"1",id:10,children:[{authName1:"11",id:1,path:'users'}]},
      {authName:"",id:20,children:[{authName1:"21",id:1,path:'rules'},{authName1:"22",id:2,path:'rigths'}]},
      {authName:"3",id:30,children:[{authName1:"31",id:1,path:'user33'},{authName1:"32",id:2,path:'user33'},{authName1:"33",id:3,path:'user33'}]}
    ]*/
  },
  methods:{
  //
    logout:function (){
      //token
      this.$http('/loginout').then(res=>{
        if (res.data.code ==200){
          this.$message.success("")
          //token
          window.sessionStorage.clear();
          //
          this.$router.push('/login')
        }else this.$message.error("")
      })

    },

    //
    getMenuList:function (){
      this.$http('/home/menulist').then(res=>{
        if (res.data.code !=200){
          this.$message.error(',')
          this.$router.push('/login')
        } else this.menList=res.data.data;
      })
    },

    //
    togglecollapse:function (){
      this.iscollapse= !this.iscollapse
    },

    /**/
    saveNavState(activePth){
      window.sessionStorage.setItem('activePth',activePth)
      this.activePth=activePth
    }


  }
}
</script>

<style lang="less" scoped>
.el-header{
  background-color: #373d41;
  display: flex;
  justify-content: space-between;
  padding-left: 0px;
  align-items: center; /**/
  color: #fff;
  font-size: 20px;
  >div{
    display: flex;
    align-items: center;
    span{
      margin-left: 15px;
    }
    img{
      width: 50px;
      border-radius: 25px;
    }
  }
}
.el-aside{
  background-color: #333744;
}
.el-main{
  background-color: #EAEDF1;
}
.el-menu{
  border-right: none;
}
.home-container{
  height: 100%;
}
.toggele-button{
  background-color:#4A5064 ;
  font-size: 10px;
  line-height: 24px;
  color: #fff;
  text-align: center;
  letter-spacing: 0.2em;
  cursor: pointer;
}
.user-avatar {
  cursor: pointer;
  width: 40px;
  height: 40px;
  border-radius: 10px;
}
</style>
