import Vue from 'vue'
import Router from 'vue-router'
import Login from "@/components/Login";
import home from "@/components/home"
import welcome from "@/components/Welcome"
import Users from "@/components/user/Users"
import FineReport from "../components/FineReport/FineReport"
import message from "@/components/user/message";
import zxyh from "@/components/xtjk/zxyh";
import server from "../components/server/index"
import ybdjcx from "../components/YB/YbDjcx"
import ybjscx from "../components/YB/YbJscx"
/*const home=()=>import('../views/home/Home')
const cart=()=>import('../views/cart/Cart')
const profile=()=>import('../views/profile/Profile')
const category=()=>import('../views/category/Category')*/

Vue.use(Router)

const routes=[
    {
        path: '/',
        redirect: 'login',
    },
    {
        path: '/login',
        component: Login,
    },
    {
        path: '/home',
        component: home,
        redirect: '/welcome',
        children:[
            {
                path:'/welcome',
                component: welcome
            },
            {
                path:'/users',
                component: Users
            },
            {
                path:'/finereport',
                component: FineReport
            },
            {
                path:'/message',
                component: message
            },
            {
                path:'/zxyh',
                component: zxyh
            },
            {
                path:'/server',
                component: server
            },
            {
                path:'/ybdjcx',
                component: ybdjcx
            },
            {
                path:'/ybjscx',
                component: ybjscx
            }
            ]
    }

]

const router=new Router({
    routes:routes,
    //mode:'history', //hash#
})

//beforeEach
//tofromnext
router.beforeEach((to,from,next)=>{
    //login
    if(to.path==='/login') return next();
    //tokenlogin
    const token=window.sessionStorage.getItem('token')
    if(!token) return next('/login')
    //
    next()
})

export default  router
